import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, LoadingController, AlertController, NavController } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { Storage } from  '@ionic/storage'
import { combineAll } from 'rxjs-compat/operator/combineAll';
import { i18nMetaToJSDoc } from '@angular/compiler/src/render3/view/i18n/meta';
import { NavparamService } from '../navparam.service';

@Component({
  selector: 'app-create-store',
  templateUrl: './create-store.page.html',
  styleUrls: ['./create-store.page.scss'],
})
export class CreateStorePage implements OnInit {


  image: any="";
  store_name: any="";
  disabledButton;
  datastorage: any;
  user_id: any;
  sid: any;
  xyz: any = 2;

  constructor(
    private http: HttpClient,
    private router: Router,
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private accessProviders: AccessProviders,
    private storage: Storage,
    private navController: NavController,
    private navParamService: NavparamService
  ) { }
  
  ngOnInit() {
  }

  selectedFile(event) {
    this.image = event.target.files[0]; 
  }

  ionViewWillEnter() {
    this.storage.get('storage_xxx').then((res)=>{
      console.log(res);
        this.datastorage = res;
        this.user_id = this.datastorage.user_id;
        this.sid = this.datastorage.store_id;
    });
    
  } 
  
  async tryCreate() {
    this.navParamService.setNavData(1);
    if(this.store_name==""){
      this.presentToast('Store Name is required');
    }//else if(this.image==""){
     // this.presentToast('Store Image is required');}
    else{

    const loader = await this.loadingCtrl.create({
      message: 'Please wait.....',
    });
    loader.present();

    return new Promise(resolve =>{
      let body = {
        aksi: 'process_create-store',
        store_name: this.store_name,
        user_id: this.user_id 
        
      }
      this.accessProviders.postData(body, 'process_create-store.php').subscribe((res:any) => {
        if(res.success == true) {
          this.navParamService.setNavData(1); 
          //const formData = new FormData();
        //  formData.append('image', this.image);
         // formData.append('id', this.user_id);
        //  this.http.post('http://localhost/Capstone2/capstone_project_backend_001/create_store_image.php', formData).subscribe((response: any)=>{
        //    console.log(response);
        //   });
           loader.dismiss();
           console.log(res.success);
           console.log(res.msg);
           this.disabledButton = false;
          this.presentToast(res.msg);
          this.navParamService.setStoreId(res.sid);
          this.router.navigate(['/panel-vendor']);
        } else {
          loader.dismiss();
          this.disabledButton = false;
          this.presentToast(res.msg); 
        }
      });   
    });
    }

  }
  

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500,
      position: 'bottom'
    });
    toast.present();
  }

}
